body {
  margin: 0;
  padding: 0;
  margin-top:0;
}

* {
    box-sizing: border-box;
}


html {
    margin-top:0;
    font-family: "Lucida Sans", sans-serif;
}

h1 {
    margin-top:0;
    border-radius: 15px 50px 30px 5px;
    font-size: 40px;
    background-color: black;
    color: white;
}

table {
	border-radius: 15px 15px 15px 15px;
}

div {
  margin-top:0;
	border-radius: 15px 15px 15px 15px;
	margin: 0;
  padding: 0;
}

.darkblue {
	background-color: darkblue;		
	color: white;
}

.lightblue {
	background-color: lightblue;		
	color: white;
}

.oddeven:nth-child(even) {background: lightblue}
.oddeven:nth-child(odd) {background: lightpink}

.wordwrap { 
   overflow-wrap: break-word;
   word-wrap: break-word;
}

@media only screen and (max-width: 600px) {
    body {
          background: green;
    }
    .oddeven:nth-child(even) {background: lightgreen}
    .oddeven:nth-child(odd) {background: lightpink}

}
